.App {
  display: flex;
  flex-direction: column;
  margin: 0 4% 0;
}

.App-header {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: 2rem 0 0;
}
.search {
  display: flex;
}
.search-label {
  display: flex;
  flex: 1;
  width: 150px;
}
.search-input {
  flex: 1;
}
.search-content {
  margin: 0 5%;
}

.App-body {
  margin-top: 7rem;
}

.App-footer {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
}
.App-footer__content {
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 4rem 0;
}